<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name:'main'}">
            
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name:'cart'}">
            
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
             
          </a>
        </li>
      </ul>

      <h1 class="content__title">
        
      </h1>
      <span class="content__info">
        3 
      </span>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST" @submit.prevent="validateForm">
        <div class="cart__field">
          <div v-if="sendingBasket">
            <div class="preloader">
              <svg class="preloader__image" role="img" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512">
                <path fill="currentColor"
                      d="M304 48c0 26.51-21.49 48-48 48s-48-21.49-48-48 21.49-48 48-48 48 21.49 48 48zm-48 368c-26.51 0-48 21.49-48 48s21.49 48 48 48 48-21.49 48-48-21.49-48-48-48zm208-208c-26.51 0-48 21.49-48 48s21.49 48 48 48 48-21.49 48-48-21.49-48-48-48zM96 256c0-26.51-21.49-48-48-48S0 229.49 0 256s21.49 48 48 48 48-21.49 48-48zm12.922 99.078c-26.51 0-48 21.49-48 48s21.49 48 48 48 48-21.49 48-48c0-26.509-21.491-48-48-48zm294.156 0c-26.51 0-48 21.49-48 48s21.49 48 48 48 48-21.49 48-48c0-26.509-21.49-48-48-48zM108.922 60.922c-26.51 0-48 21.49-48 48s21.49 48 48 48 48-21.49 48-48-21.491-48-48-48z">
                </path>
              </svg>
            </div>
          </div>
          <div v-else class="cart__data">
            <BaseFormText v-model="formData.name" title="" :error="formError.name" :front="front.name"
                          placeholder="   "/>
            <BaseFormText v-model="formData.address" title=" " :error="formError.address" :front="front.address"
                          placeholder="  "/>
            <BaseFormText v-model="formData.phone" title="" :error="formError.phone" :front="front.phone"
                          placeholder="  "/>
            <BaseFormText v-model="formData.email" title="Email" :error="formError.email" :front="front.email"
                          placeholder="  Email"/>
            <BaseFormTextArea v-model="formData.comment" title="  " :error="formError.comment" :front="front.comment"
                              placeholder=" "/>
          </div>

          <div class="cart__options">methodsa
            <h3 class="cart__title"></h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="delivery" value="0" checked="">
                  <span class="options__value">
                     <b></b>
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="delivery" value="500">
                  <span class="options__value">
                     <b>500 </b>
                  </span>
                </label>
              </li>
            </ul>

            <h3 class="cart__title"></h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="pay" value="card">
                  <span class="options__value">
                      
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="pay" value="cash">
                  <span class="options__value">
                      
                  </span>
                </label>
              </li>
            </ul>
          </div>
        </div>

        <div class="cart__block">
          <ul class="cart__orders">
            <li class="cart__order" v-for="item in products" :key="item.productId">
              <h3>{{ item.product.title }}</h3>
              <b>{{ (item.product.price * item.amount)|numberFormat }} </b>
              <span>: {{ item.product.id }}</span>
            </li>
          </ul>

          <div class="cart__total">
            <p>: <b>{{ productDeliveryPrice|numberFormat }} </b></p>
            <p>: <b>{{ $store.getters.cartTotalItems }}</b> {{ howManyItems }}  
              <b>{{ totalPriceDelivery|numberFormat }} </b></p>
          </div>

          <button class="cart__button button button--primery" type="submit">
             
          </button>
        </div>
        <div class="cart__error form__error-block" v-if="formErrorMessage">
          <h4>  !</h4>
          <p>
            {{ formErrorMessage }}
          </p>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import BaseFormText from "@/components/BaseFormText";
import BaseFormTextArea from "@/components/BaseFormTextArea";
import howManyItems from "@/helpers/howManyItems";
import {mapGetters} from 'vuex';
import {mapState} from 'vuex';
import numberFormat from "@/helpers/numberFormat";
import axios from "axios";
import {API_BASE_URL} from "../../config";

export default {
  name: "OrderPage",
  data() {
    return {
      formData: {},
      formError: {},
      formErrorMessage: '',
      sendingBasket: false,
      errors:[],
      front:{
        name:'',
        address:'',
        phone:'',
        email:''
      }
    }
  },
  components: {BaseFormText, BaseFormTextArea},
  computed: {
    howManyItems,
    ...mapGetters({totalPrice: 'cartTotalPrice'}),
    ...mapState({deliveryPrice: 'productDeliveryPrice'}),
    products() {
      return this.$store.getters.cartDetailProducts
    },
    totalPriceDelivery() {
      return this.totalPrice + this.deliveryPrice
    },
    productDeliveryPrice() {
      return this.deliveryPrice
    }

  },
  filters: {
    numberFormat
  },
  methods: {
    validEmail: function (email) {
      let pattern = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
      return pattern.test(email);
    },
    validPhone: function (phone){
      let pattern =/^\+\d{11}$/;
      return pattern.test(phone);
    },
    validateForm() {
      this.errors=[]
      this.front.name=''
      this.front.address=''
      this.front.phone=''
      this.front.email=''
      if (!this.formData.name) {
        this.errors.push('')
        this.front.name = '   '
      }
      if (!this.formData.address) {
        this.errors.push('')
        this.front.address = '   '
      }
      if (!this.formData.phone) {
        this.errors.push('')
        this.front.phone = '     '
      } else if (!this.validPhone(this.formData.phone)){
        this.errors.push('')
        this.front.phone = '   '
      }
      if (!this.formData.email) {
        this.errors.push('')
        this.front.email = '  Email '
      } else if (!this.validEmail(this.formData.email)) {
        this.errors.push('')
        this.front.email = '    '
      }
      if (!this.errors.length) {
        this.order();
      }
    },
    order() {
      this.formError = {};
      this.formErrorMessage = '';
      this.sendingBasket = true;

      axios
        .post(API_BASE_URL + '/api/orders', {
          ...this.formData
        }, {
          params: {
            userAccessKey: this.$store.state.userAccessKey
          }
        })
        .then((response) => {
          this.$store.commit('resetCart');
          this.sendingBasket = false;
          this.$store.commit('updateOrderInfo', response.data);
          this.$router.push({name: 'orderInfo', params: {id: response.data.id}});
        })
        .catch(error => {
          this.formError = error.response.data.error.request || {};
          this.formErrorMessage = error.response.data.error.message;
          this.sendingBasket = false;
        })
    }
  }
}
</script>

<style scoped>
.preloader {
  margin: 0 auto;
}
</style>
